@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

.button__call__part {
	position: absolute;
	display: inline-block;
	left: 8px;
	top: 30px;
	border-radius: 4px;
	border-width: 1px;
	border-color: rgba(225, 210, 197, 0.5);
	border-radius: 4px;
	border-style: solid;
	width: 40px;
	height: 40px;
	cursor: pointer;
	z-index: 2;
	animation-name: fadeIn;
	animation-duration: 5s;
	animation-fill-mode: forwards;
	transition: 1s;
}

.button__call__part:hover {
	/*background-color: rgba(245, 98, 94, 0.6);*/
	background-color: #67277a;
}

.button__call__part:before {
	content: "";
	display: inline-block;
	height: 100%;
	vertical-align: middle;
}

.button__call__part img {
	width: 100%;
	vertical-align: middle;
	filter: invert(100%);
}

.static_content {
	padding-left: 20px;
	height: calc(98% - 20px);
	min-width: 768px;
}

.type_wrapper {
	padding: 0 20px;
	min-width: 768px;
	height: 5%;
	display: flex;
	align-items: center;
	font-size: 16px;
	font-weight: 600;
	letter-spacing: 2px;
	white-space: nowrap;
	box-shadow: 0 -5px 5px rgba(0, 0, 0, 0.05);
}

.information_icon {
	display: inline-block;
	background-size: 100%;
	background-repeat: no-repeat;
	background-image: url(data:image/svg+xml;utf8;base64,);
	width: 15px;
	height: 15px;
	vertical-align: middle;
	margin-left: 10px;
	cursor: pointer;
}

/* section style */
table {
	margin-top: 8px;
}

table td:first-child {
	min-width: 150px;
	padding-right: 30px;
}

table td {
	vertical-align: top;
	padding-bottom: 16px;
}

table td div {
	padding-bottom: 2px;
}

.section_name,
.section_summary,
.section_skill,
.section_work,
.section_project,
.section_education {
	padding-top: 20px;
}

.section_name {
	font-size: 32px;
	font-weight: 600;
}

.section_skill ul {
	margin: 0;
	padding: 0;
	list-style: none;
	columns: 2;
}

.section_skill ul li {
	padding: 4px 0;
}

.title {
	font-weight: 600;
	font-size: 24px;
	margin-bottom: 4px;
}

.about_modal {
	background-color: #fff;
	padding: 16px;
	border-radius: 4px;
	max-width: 80%;
	margin: auto;
}

.about_modal img {
	height: auto;
	width: 100%;
}

a {
	text-decoration: none;
	color: rgba(255, 10, 10, 0.5);
}
